@import "~styles/bundle.scss";

.block-description {
  display: grid;
  gap: 1em;

  text-align: center;
  line-height: 1.4;

  @include Container;

  h2 {
    margin: unset;

    font-size: 2.875rem;
    font-weight: 700;

    em {
      font-style: normal;

      @include TextBackground($color-yellow);
    }

    @include Tablet {
      font-size: 1.5rem;
    }

    @include Mobile {
      font-size: 1.25rem;
    }
  }

  p {
    margin: unset;

    font-size: 1.5rem;
    font-weight: 400;

    color: $color-gray;

    @include Tablet {
      font-size: 1rem;
    }

    @include Mobile {
      font-size: 0.875rem;
    }
  }
}