.custom-link {
  font-style: normal;
  font-weight: 700;
  white-space: nowrap;
  font-size: 1.25rem;
  line-height: 120%;
  color: #FFFFFF;
  text-decoration: none;

  &::after {
    border-bottom: 2px solid #F1CE8C;
    content: "";
    display: block;
    margin: 0 auto;
    transition: width 250ms ease-in-out 0s;
    width: 0;
  }

  &:hover:after {
    width: 100%;
    transition: width 0.3s ease-in-out;
  }
}