@import '~styles/bundle';


.title {
  font-style: normal;
  font-weight: 700;
  font-size: 2rem;
  line-height: 140%;
  color: white;
  margin: unset;

  @include DesktopLowResolution {
    font-size: 1.5rem;
  }

  @include Tablet {
    font-size: 1.25rem;
  }

  @include Mobile {
    font-size: 1rem;
  }

  &--orange {
    color: #F1CE8C;
  }
}