@import "~styles/bundle.scss";

.gamification {
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding: 0 12.5em;
  box-sizing: border-box;
  display: flex;
  position: relative;
  margin: 15em 0;
  height: 44.5em;

  @include DesktopLowResolution {
    margin: 6.25em 0;
  }
}

.gamification__card {
  position: absolute;
  width: 22em;
  height: 44.5em;
  transition: transform 0.5s ease;

  @include DesktopLowResolution {
    width: 12.75em;
    height: 25.5em;
  }

  @include Mobile {
    width: 6em;
    height: 12em;
  }

  &--1 {
    left: 10%;
    &:hover {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: translateY(-4em);
    }
  }

  &--2 {
    left: 25%;
    transform: scale(1.15);
    z-index: 5;
    &:hover {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: translateY(-4em) scale(1.15);
    }
  }

  &--3 {
    transform: scale(1.3);
    z-index: 10;

    &:hover {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: translateY(-4em) scale(1.3);
    }
  }

  &--4 {
    right: 25%;
    transform: scale(1.15);
    z-index: 5;

    &:hover {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: translateY(-4em) scale(1.15);
    }
  }

  &--5 {
    right: 10%;
    &:hover {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: translateY(-4em);
    }
  }
}