@import 'src/styles/bundle.scss';

.first-block {
  @include Container;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;

  @include Mobile {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
    max-width: 48em;

    @include DesktopLowResolution {
      max-width: calc(100vw - 35.75em);
    }

    @include Tablet {
      max-width: calc(100vw - 28em);
    }

    @include Tablet {
      max-width: 100%;
    }
  }

  &__logo {
    max-width: 46.25em;
    width: 100%;
    margin: 6.25em 0 0;
    height: 100%;

    @include DesktopLowResolution {
      max-width: calc(100vw - 35.75em);;
    }

    @include Tablet {
      max-width: calc(100vw - 28em);;
    }
    @include Mobile {
      max-width: 100%;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 2em;
    margin-top: 4.75em;

    @include DesktopLowResolution {
      margin-top: 2.625em;
    }

    @include Tablet {
      margin-top: 1.5em;
      gap: 1em;
    }
  }

  &__button-container {
    max-width: 26em;

    @include Tablet {
      max-width: 100%;
    }
  }
}