.nft-page {
  --rigs-width: 23.75em;

  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  filter: drop-shadow(30px 4px 150px rgba(18, 165, 217, 0.7));

  &--gamification {
    left: 0;
  }

  &__border {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__content {
    position: absolute;
    width: 91%;
    height: 96%;
  }
}