@import "~styles/bundle";

.mining {
  display: flex;
  gap: 5em;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
}

.mining__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2em;

  @include Tablet {
    gap: 1.25em;
  }
}

.data-center {
  height: calc(133vw - 580px);
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  @include Tablet {
    height: calc(156vw - 500px);
  }

  @include Mobile {
    height: calc(200vw - 260px);
  }
}

.mobile-shield {
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;

  img {
    width: 49.5em;
    height: 59em;
    box-sizing: border-box;
    filter: drop-shadow(40px 4px 250px rgba(6, 210, 255, 0.4));
    object-fit: contain;

    @include Tablet {
      width: 35em;
      height: 42em;
    }

    @include Mobile {
      width: 100%;
      height: unset;
    }
  }
}

.boost {
  position: relative;
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
}

.boost__line {
  width: 1.25em;
  height: 28em;
  background: #00D6FF;
  position: absolute;
  z-index: -1;
  bottom: 25%;

  @include DesktopLowResolution {
      height: 20em;
  }

  @include Mobile {
    height: 6em;
    width: 1em;
  }
}

.energy {
  height: calc(80vw - 580px);

  @include DesktopLowResolution {
    height: 60vw;
  }
}

.boost__image {
  width: 49.5em;
  height: 59em;
  box-sizing: border-box;
  filter: drop-shadow(40px 4px 250px rgba(6, 210, 255, 0.4));
  object-fit: contain;

  @include DesktopLowResolution {
    width: 40em;
    height: 40em;
  }

  @include Mobile {
    width: 16em;
    height: unset;
  }
  &--boost {
    width: 32em;
    height: 32em;

    @include DesktopLowResolution {
      width: 20em;
      height: 20em;
    }

    @include Mobile {
      width: 8em;
      height: unset;
    }
  }
}

.boost__button {
  width: 33em;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  display: flex;

  @include DesktopLowResolution {
    width: 26em;
  }

  @include Mobile {
    width: 16em;
  }
}