@import '~styles/bundle';

.block-section {
  display: grid;
  gap: 2em;
  width: 100%;
  overflow: hidden;
  align-content: baseline;
  justify-items: center;

  padding-top: 4.5em;
  padding-bottom: 20em;

  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center top;

  &--energy {
    background-position: center bottom;
  }

  @include DesktopLowResolution {
    padding-top: 0;
    padding-bottom: 0;
    background-position: center bottom;
  }
}