@import "~styles/bundle";

.game {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25em;

  @include Tablet {
    margin-bottom: 12.5em;
  }

  @include Mobile {
    flex-direction: column;
    margin: 0 1.25em 6.25em;
  }
  &__block {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 12.5em;
    position: relative;
    left: -10em;

    @include Tablet {
      width: max-content;
      position: static;
      margin-bottom: 1.25em;
    }

    @include Mobile {
      width: unset;
      height: unset;
      margin: 0 1.25em 1.25em;
    }

    &_info {
      left: 0;
    }
  }

  &__info-gradient {
    background: linear-gradient(90deg, #F79862 0%, #FE5151 100%);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-size: 3.25rem;
    line-height: 140%;
    font-weight: 700;
    font-style: normal;
    margin: 0;

    @include Tablet {
      font-size: 2rem;
    }

    @include Mobile {
      font-size: 1.25rem;
    }
    &_green {
      background: linear-gradient(95.31deg, #27B77A 1.44%, #54F91A 98.5%);
      -webkit-text-fill-color: transparent;
      -webkit-background-clip: text;
    }
  }
}

.game-card {
  width: 100%;
  height: 100%;
  position: relative;
  max-width: 60em;

  @include Tablet {
    max-width: 35em;
  }

  &__background {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    background: #12A5D9;
    opacity: 0.8;
    filter: blur(250px);
    transform: rotate(36.52deg);
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: relative;
    z-index: 1;
  }
}