@import "~styles/bundle";

.rig-stats {
  --rigs-width: 23.75em;

  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;

  &--rotate {
    transform: rotate(-11deg);
    width: var(--rigs-width);
    height: calc(var(--rigs-width) * 2);
    transition: transform .5s ease-in-out;

    @include DesktopLowResolution {
      --rigs-width: 12.75em;
    }

    @include Mobile {
      --rigs-width: 8.75em;
    }
  }

  filter: drop-shadow(-9px -5px 150px rgba(18, 165, 217, 0.7));


  &__border {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__content {
    position: absolute;
    width: 91%;
    height: 96%;
  }
}