@import 'src/styles/bundle.scss';

.application {
  @include Container;
  display: grid;
  grid-template-columns: 55fr 45fr;
  align-items: center;

  @include Tablet {
    grid-template-columns: 1fr;
    margin-bottom: 8em;
  }

  @include Mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5em;
  }

  &__container-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 4.75em;
    position: relative;
    z-index: 2;
    margin-top: 12.5em;

    @include Tablet {
      margin-top: 0;
    }
  }

  &__container-image {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.25em;
    position: relative;
    z-index: 1;
    margin-top: 12.5em;
  }

  &__text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 2em;

    @include Tablet {
      p {
        max-width: calc(100vw - 32em);
      }
    }

    @include Mobile {
      align-items: center;
      text-align: center;
      p {
        max-width: 100%;
      }
    }

    &--buttons {
      @include Mobile {
        width: 100%;
        align-items: flex-start;
      }
    }
  }

  &__buttons {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 13.75em);
    gap: 1.25em;

    @include Tablet {
      grid-template-columns: repeat(1, 13.75em);
    }

    @include Mobile {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap;
    }
  }

  &__image {
    width: 23.75em;
    height: 100%;
    position: absolute;
    top: 0;

    &-container {
      position: relative;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: flex-end;
      display: grid;
      grid-template-columns: 1fr 1fr;

      @include DesktopLowResolution {
        width: 25em;
        justify-self: end;
      }
      @include Tablet {
        width: 20em;
        right: 4em;
        justify-self: end;
        position: absolute;
      }

      @include Mobile {
        justify-self: end;
        display: flex;
        align-items: center;
        right: unset;
        justify-content: center;
        position: relative;
        height: 20em;
        width: 50%;
      }
      &:hover {
        .rig-stats--rotate,
        .rigs-page--rotate {
          cursor: pointer;
          transition: transform .5s ease-in-out;
          @include Tablet {
            transform: rotate(-11deg);
          }
        }

        .rig-stats--rotate {
          transform: rotate(-17deg) translate(-2em, -2em);
          @include Tablet {
            transform: rotate(-11deg);
          }
        }

        .rigs-page--rotate {
          transform: rotate(12.37deg) translate(2em, -2em);
          @include Tablet {
            transform: rotate(6.37deg);
          }
        }
      }
    }
  }
}