@import 'src/styles/bundle.scss';

.description {
  font-style: normal;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 140%;
  color: white;
  margin: unset;

  @include DesktopLowResolution {
    font-size: 1rem;
  }

  @include Mobile {
    font-size: 0.875rem;
  }

  &--dark {
    color: #B4B4B4;
  }

  &--big {
    font-size: 2em;

    @include DesktopLowResolution {
      font-size: 1.5rem;
    }

    @include Tablet {
      font-size: 1rem;
    }
  }

  &--left {
    text-align: left;
  }

  &--center {
    text-align: center;
  }

  &--right {
    text-align: right;
  }
}