@import "./_mixins.scss";

* {
  box-sizing: border-box;
}

body {
  margin: unset;
  font: 16px/1 "Whyte", Arial, sans-serif;
  color: white;
  scroll-behavior: smooth;

  background: url("../assets/bg.png") top/contain no-repeat black;

  @include Mobile {
    background: black url("../assets/bg.png") no-repeat right 0;
    background-size: 170vw;
  }
}

em {
  color: #f1ce8c;
  font-style: normal;
}