@import "~styles/bundle.scss";

.block-pointer {
  display: grid;
  gap: 2em;

  align-content: baseline;
  justify-items: center;
}

.block-pointer__title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  line-height: 1.4;

  color: $color-yellow;

  @include DesktopLowResolution {
    font-size: 1.5rem;
  }

  @include Tablet {
    font-size: 1.25rem;
  }

  @include Mobile {
    font-size: 1rem;
  }
}

.block-pointer__pointer {
  display: grid;
  justify-items: center;
}

.block-pointer__tag {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 10rem;
  height: 10rem;

  background: linear-gradient(94.01deg, #ECE7E2 16.37%, #F1CE8C 91.65%);
  border-radius: 50%;

  font-size: 5rem;
  font-weight: 700;

  color: #262222;

  @include Tablet {
    width: 8.75rem;
    height: 8.75rem;
  }

  @include Mobile {
    width: 6.25rem;
    height: 6.25rem;
    font-size: 3.75rem;
  }
}

.block-pointer__line {
  width: 0.625em;
  height: 10em;

  background: linear-gradient(179.89deg, #EFDBB9 0.09%, rgba(241, 206, 140, 0) 91.01%);

  @include Mobile {
    height: 5em;
  }
}