@import "~styles/bundle";

.custom-button-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: linear-gradient(89.93deg, #16D9E3 4.29%, #30C7EC 26.4%, #46AEF7 51.96%, #139AFA 89.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-style: normal;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 120%;
  white-space: nowrap;
  border: 2px solid rgba(22, 217, 227, 1);
  padding: 1.875rem;
  box-sizing: border-box;

  filter: drop-shadow(0px 0px 24px rgba(67, 178, 246, 0.48)) drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 6px;
  transition: background .3s, color .3s, box-shadow .3s, filter .3s;


  @include Tablet {
    padding: 1.25rem;
  }

  @include Mobile {
    padding: 1rem;
    font-size: 1rem;
  }

  &:hover {
    background: linear-gradient(89.93deg, #16D9E3 4.29%, #30C7EC 26.4%, #46AEF7 51.96%, #139AFA 89.96%);
    box-shadow: 0px 0px 24px rgba(67, 178, 246, 0.48);
    transition: background .3s, color .3s, box-shadow .3s, filter .3s;
    color: white;
    -webkit-background-clip: unset;
    -webkit-text-fill-color: unset;
    text-decoration: unset;
  }

  &--yellow {
    background: linear-gradient(94.01deg, #ECE7E2 16.37%, #F1CE8C 91.65%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25)) drop-shadow(0px 0px 24px rgba(241, 206, 140, 0.48));
    border: 2px solid #F1CE8C;

    &:hover {
      text-decoration: unset;
      background: linear-gradient(94.01deg, #ECE7E2 16.37%, #F1CE8C 91.65%);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      color: #262222;
      transition: background .3s, color .3s, box-shadow .3s, filter .3s;
    }
  }

  &--small-padding {
    padding: 1rem;
  }
}