@import '../../styles/bundle.scss';

.header {
  @include Container;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;

  @include DesktopLowResolution {
    margin-top: 1.5rem;
  }

  &__logo {
    height: 4.5em;
    width: 15.5em;

    @include DesktopLowResolution {
      height: 3.25em;
      width: 13em;
    }
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 13.625rem;
    height: 3.5rem;
    background: linear-gradient(89.93deg, #16D9E3 13.29%, #30C7EC 32.4%, #46AEF7 53.96%, #139AFA 53.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: normal;
    font-weight: 700;
    font-size: 1.25rem;
    line-height: 120%;
    border: 0.125rem solid rgba(22, 217, 227, 1);
    filter: drop-shadow(0px 0px 1.5rem rgba(67, 178, 246, 0.48)) drop-shadow(0px 0.25rem 0.25rem rgba(0, 0, 0, 0.25));
    border-radius: 0.375rem;
  }
}

.header__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 6.25em;

  @include DesktopLowResolution {
    gap: 1.875em;
  }

  @include Tablet {
    display: none;
  }

  &--mobile {
    display: none;


    @include Tablet {
      display: flex;
      cursor: pointer;
    }
  }
}


.modal-navigation {
  display: none;

  &[open] {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    position: fixed;
    top: 0;
    margin: unset;
    z-index: 99999;
    background: transparent;
    right: 0;
  }
}

.modal-navigation__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: min(100%, 25em);
  height: 100%;
  background: linear-gradient(184.34deg, #000001 38.7%, #03060E 83.51%, #141D25 100%), #614CD4;
  top: 0;
  right: 0;
  gap: 2em;
  position: absolute;
  box-sizing: border-box;
  padding: 2em;
}

.modal-navigation__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.modal-navigation__title {
  font-style: normal;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 120%;
  color: #FFFFFF;
  margin: 0;
}

.modal-navigation__close {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  background: none;
  border: none;
  cursor: pointer;
}

.modal-navigation__body {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.modal-navigation__links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  gap: 2em;
}
.modal-navigation__logo {
  align-self: center;
}