@import '~styles/bundle';

.card {
  background: linear-gradient(145.32deg, rgba(23, 23, 23, 0.4) 6.49%, rgba(255, 255, 255, 0.012) 59.28%, rgba(32, 32, 32, 0.144) 113.18%), radial-gradient(100% 100% at 0% 0%, rgba(94, 104, 113, 0.4) 0%, rgba(99, 99, 99, 0.16) 52.43%, rgba(50, 53, 56, 0.4) 100%);
  border: 0.125em solid;
  border-image-source: radial-gradient(100% 100% at 0% 0%, rgba(94, 104, 113, 0.4) 0%, rgba(99, 99, 99, 0.16) 52.43%, rgba(50, 53, 56, 0.4) 100%);
  border-radius: 3.125em;
  width: 100%;
  height: 30em;
  display: flex;
  object-fit: contain;
  position: relative;
  flex-direction: column;
  align-items: center;
  gap: 3.125em;
  justify-content: flex-end;
  padding: 2.5em;
  box-sizing: border-box;
  overflow: hidden;

    @include Tablet {
        height: 20em;
        padding: 1.875em;
    }

  &:hover {
    cursor: pointer;

    .card__image {
      transform: scale(1.1);
      transition: transform .3s ease-in-out;
    }
  }

  &__color {
    background: #12A5D9;
    filter: blur(4em);
    width: 100%;
    height: 100%;
    max-width: 14em;
    max-height: 14em;
    border-radius: 50%;
    position: absolute;
  }

  &__image {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: transform .3s ease-in-out;
  }
}

.card__image-container {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: relative;
  object-fit: contain;
}

.card__text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}