@import 'src/styles/bundle.scss';

.title-component {
  font-style: normal;
  font-weight: 700;
  font-size: 2.875rem;
  line-height: 140%;
  margin: unset;

  @include DesktopLowResolution {
    font-size: 2rem;
  }

  @include Tablet {
    font-size: 1.5rem;
  }

  @include Mobile {
    font-size: 1.25rem;
  }
}