@import 'src/styles/bundle.scss';

.show-more-component {
  background: linear-gradient(170deg, transparent 0%, rgba(255, 255, 255, 0.15) 40%, transparent 80%, rgba(255, 255, 255, 0.1) 100%);
  border-radius: 5.625em;
  margin-top: 6.25em;
  backdrop-filter: blur(3.5em);
  padding: 3.125em 6.25em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include DesktopLowResolution {
    padding: 3.125em;
    border-radius: 3.25em;
  }

  @include Tablet {
    padding: 1.875em 3.125em;
    border-radius: 1.875em;
  }

  @include Mobile {
    padding: 1.25em;
    border-radius: 1.25em;
  }

  &__block {
    margin-top: 2em;
    height: 2em;
    overflow: hidden;
    transition: 350ms ease height;

    @include Tablet {
      height: 1.25em;
    }

    &_open {
      height: 25em;

      @include DesktopLowResolution {
        height: 18em;
      }

      @include Tablet {
        height: 13em;
      }

        @include Mobile {
            height: 24em;
        }
    }
  }

  &__button {
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2em;
    background: transparent;
    border: none;
    color: #B4B4B4;
    transition: color 0.1s ease-in-out;
    align-self: center;

    @include Tablet {
      height: 3.75em;
      svg {
        height: 100%;
      }
    }

    &:hover {
      cursor: pointer;
      color: #16d9e3;
    }
  }
}