@import 'src/styles/bundle.scss';

.products {
  padding: 0 12.5em;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 6.25em;
  margin-top: 12.5em;

  @include DesktopLowResolution {
    margin-top: 6.5em;
    gap: 4.375em;
    padding: 0 6.25em;
  }

  @include Tablet {
    gap: 1.25em;
  }

  @include Mobile {
    padding: 0 1.25em;
    margin-top: 3.125em;
    gap: 2.5em;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    flex-direction: row;
    align-items: center;
    gap: 1.25em;
    width: 100%;

    @include DesktopLowResolution {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    @include Mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1.25em;
    }
  }
}