@import '~styles/bundle';

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(266.95deg, #06090E 35.08%, #000000 67.26%);
  padding: 5.5em 12.5em;

  @include DesktopLowResolution {
    padding: 5.5em 6.25em;
  }

  @include Tablet {
    padding: 3.125em;
  }

  @include Mobile {
    flex-direction: column;
    padding: 3.125em 1.25em;
    gap: 2em;
  }
}

.footer__links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0;
  gap: 1em;
  margin: 0;
  list-style: none;

  @include Mobile {
    align-items: center;
    justify-content: center;
    gap: 2em;
  }
}

.footer__social {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
  gap: 2em;

  @include Mobile {
    align-items: center;
    justify-content: center;
    gap: 2em;
  }
}

.footer__item {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2em;
}

.footer__description {
  width: 100%;
  text-align: right;
  font-style: normal;
  font-size: 1.5em;

  @include Tablet {
    font-size: 1em;
  }

  @include Mobile {
    text-align: center;
  }
}