@import '~styles/bundle';

.partners {
  display: grid;
  align-items: center;
  justify-content: center;
  width: 100%;
  box-sizing: border-box;
  margin-top: 8em;
}

.partners__container {
  @include Container;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.25em;
  box-sizing: border-box;
  margin-top: 6.25em;
  margin-bottom: 12.5em;

  @include DesktopLowResolution {
    margin-top: 3.125em;
    margin-bottom: 6.25em;
  }

  @include Mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.25em;
  }
}

.partners__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 21em;
  box-sizing: border-box;
  background: linear-gradient(145.32deg, rgba(23, 23, 23, 0.4) 6.49%, rgba(255, 255, 255, 0.012) 59.28%, rgba(32, 32, 32, 0.144) 113.18%), radial-gradient(100% 100% at 0% 0%, rgba(94, 104, 113, 0.4) 0%, rgba(99, 99, 99, 0.16) 52.43%, rgba(50, 53, 56, 0.4) 100%);
  border-radius: 50px;

  @include Tablet {
    height: 11em;
  }
  &:hover {
    .partners__logo {
      cursor: pointer;
      transition: transform 0.5s ease;
      transform: scale(1.05);
    }
  }
}

.partners__logo {
  width: 60%;
  height: 60%;
  transition: transform 0.5s ease;
  object-fit: contain;
}