$color-yellow: #f1ce8c;
$color-blue-gradient: linear-gradient(90.04deg, #16D9E3 30.48%, #30C7EC 64.42%, #46AEF7 102.69%, #139AFA 102.69%);
$color-white: #fff;
$color-black: #000;
$color-gray: #B4B4B4;


$width-desktop: 1625px;
$width-desktop-low-resolution: 1080px;
$width-tablet: 768px;

@mixin DesktopLowResolution {
	@media screen and (max-width: #{$width-desktop}) {
		@content;
	}
}

@mixin Tablet {
	@media screen and (max-width: #{$width-desktop-low-resolution}) {
		@content;
	}
}


@mixin Mobile {
	@media screen and (max-width: #{$width-tablet}) {
		@content;
	}
}

@mixin Container {
	margin: 0 12.5em;

	@include DesktopLowResolution {
		margin: 0 6.25em;
	}

	@include Mobile {
		margin: 0 1.25em;
	}
}

@mixin TextBackground($color) {
	background: $color;
	background-clip: text;
	text-fill-color: transparent;
}