@import "~styles/bundle";

.phone-coins {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include Mobile {
    height: min-content;
  }

  &__mobile {
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;

    @include Mobile {
      display: none;
    }
  }

  &__content {
    position: relative;
    width: 100%;
    height: 100%;
    @include Mobile {
      display: none;
    }
  }

  &__background {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 50em;
    background: #12A5D9;
    opacity: 0.8;
    filter: blur(250px);

    @include Mobile {
      display: none;
    }
  }

  &__coin {
    position: absolute;
    z-index: 2;

    &--flux {
      width: 12em;
      height: 12em;
      right: 10%;
      top: 40%;
      @include Tablet {
        width: 6em;
        height: 6em;
      }
    }

    &--kaspa {
      width: 11.5em;
      height: 11.5em;
      left: 10%;
      top: 35%;

      @include Tablet {
        width: 6.5em;
        height: 6.5em;
      }
    }

    &--proof {
      width: 8.75em;
      height: 8.75em;
      right: 10%;
      top: 0;

        @include Tablet {
            width: 5em;
            height: 5em;
        }
    }

    &--ravencoin {
      width: 15.375em;
      height: 15.375em;
      left: 18%;
      top: 0%;

      @include Tablet {
        width: 7.5em;
        height: 7.5em;
      }
    }

    &--ethereum {
      width: 17.5em;
      height: 17.5em;
      left: 45%;
      top: 5%;

      @include Tablet {
        width: 8.25em;
        height: 8.25em;
      }
    }

    &--coin {
      width: 7em;
      height: 7em;
      left: 65%;
      top: 0%;

      @include Tablet {
          width: 4em;
          height: 4em;
      }
    }

    &--neox {
      width: 7.5em;
      height: 7.5em;
      left: 26%;
      top: 46%;

      @include Tablet {
        width: 5em;
        height: 5em;
      }
    }

    &--ergo {
      width: 14.5em;
      height: 14.5em;
      left: 35%;
      top: 25%;

      @include Tablet {
          width: 9em;
          height: 9em;
      }
    }

    &--qi {
      width: 6.5em;
      height: 6.5em;
      left: 70%;
      top: 25%;
    }

    &--dynex {
      width: 10.25em;
      height: 10.25em;
      left: 55%;
      top: 45%;
      @include Tablet {
        width: 5.5em;
        height: 5.5em;
      }
    }
  }

  &__mobile-image {
    display: none;
    @include Mobile {
      display: block;
      width: 100%;
      height: 100%;
    }
  }
}